/* styles for login page */
.login_wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    margin-top: 30vh;
}

.login_box {
    width: 350px;
    min-height: 250px;
    border: 1px solid #E8E0CF;
    border-radius: 15px;
    background: #443F49;
    align-items: center;
    display: flex;
    padding: 15px 0;
}

.login_form {
    width: 250px;
    margin: 0 auto;
}

.login_form h5 {
    margin-bottom: 25px;
    text-align: center;
}

.login_form span {
    display: inline-block;
    width: 100%;
}

.login_form label {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    color: white;
    font-size: 18px;
    position: relative;
}

.login_form span input {
    width: 100%;
    padding: 5px;
    border-radius: 5px;
    margin-bottom: 25px;
    background: whitesmoke;
}

.login_form label input[type="checkbox"],
.login_form label input[type="checkbox"]:checked {
    width: 18px;
    height: 18px;
    margin-right: 15px;
}

.login_form > div {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
}

.login_form button {
    width: 80px;
    padding: 5px;
    border-radius: 5px;
    background: whitesmoke;
    transition: all 0.1s ease;
}

.login_form button:active {
    transform: scale(0.99);
    background: #E8E0CF;
}

.login_form span.login_captcha {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}

.login_form span.login_captcha input {
    min-width: 40%;
    width: 90%;
    margin: 0 15px 0 0;
}

.login_form span.login_captcha img {
    width: 100%;
    height: 40px;
}

.commonError {
    color: #e8e0cf;
    padding-bottom: 5px;
    bottom: 100px;
}

/* styles for login page end */


/* media styles for login */
@media screen and (max-width: 767px) {
    .login_wrapper {
        margin: 0;
    }

    .login_box {
        width: 100%;
        height: 100vh;
    }
}

/* media styles for login end */
